@import "Main.scss";

.footer-container {
  background-color: #efe6e6;
  width: 100%;
  border-radius: 2px;
  border: solid #d4c7c7c2;
  display: flex;
  z-index: 2;
  position: relative;
}

.footer-container > h2{
  font-weight: bold;
  cursor: pointer;
}

.help-section > h2,
.link-section > h2{
  font-family: $title_font;
  font-size: 18px;
}

.link-section > h2{
  margin-bottom: 20px;
}

.help-section,
.link-section > h3 {
  font-family: $title_font;
  font-size: 12px;
}

//.facebook-image,
