//  height: 45px;
//  margin-right: 10px;
//}

.facebook-image, .instagram-image{
  width: 45px;
  height: 45px;
  margin-right: 10px;
}

.snapchat-image{
  height: 80px;
  margin-right: 10px;
}

.help-section{
  display: flex;
  flex-direction: column;
  margin-left: 65px;
}

.link-section, .social-media-icons{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.link-section{
  margin-left: 250px;
}

/*
    Copyright symbol
    https://blog.hubspot.com/website/html-code-copyright
 */

.copyright-section{
  background-color: #d6cfcf;
  width: 100%;
  height: 30px;
  border-radius: 2px;
  border: solid #d4cbcb;
  display: flex;
}

.copyright-section > span{
  margin-left: 65px;
  font-family: Times New Roman, Serif, serif;
}