.admin-create-product {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 100px;
  position: relative;
  padding-left: 450px; }
  .admin-create-product h1 {
    color: black;
    font-family: Times New Roman, Serif, serif;
    font-size: 24px;
    font-weight: bold;
    position: absolute;
    margin-bottom: 950px; }
  .admin-create-product .create-form {
    border-radius: 5px;
    box-shadow: 20px 20px 50px grey;
    border-color: #A26D5D;
    border-style: outset;
    background-color: #efe6e6;
    display: flex;
    flex-direction: column;
    align-items: center;
