/* AdminTable */

.admin-head-container {
  padding-bottom: 75px;
}

.admin-body-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.admin-table-container {
  display: flex;
  justify-content: center;
  border-radius: 5px;
  margin-bottom: 400px;
  margin-top: 80px;
  margin-left: 40px;

  .admin-table-container table {
    padding: 15px;
    width: 100%;
    border-collapse: collapse;
    font-family: Arial, sans-serif;
    border: 2px solid $grey-border;
    border-radius: 5px;
  }
}

.admin-table-container thead {
  background-color: #A26D5D;
  color: $white;
  border-color: $white;
  text-transform: capitalize;
  text-align: center;
  font-weight: bold;
  font-family: $title_font;
}

.admin-table-container thead :hover {
  border-color: $grey-border;
}

.admin-table thead th,
.admin-table-container tbody td {
  padding: 20px;
  font-size: 16px;
  text-align: center;
}

.admin-table-container tbody tr {
  border-bottom: 1px solid $grey-border;
  background-color: $white;
  position: relative;
}

.admin-table-container tbody tr:hover {
  background-color: #e5e3e3;
  transition: background-color 0.6s ease;
}

.admin-table-container tbody tr:hover td {
  color: transparent;
  cursor: pointer;
}

.admin-table-container tbody tr:hover::after {
  content: "\1F50D";
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}


.admin-table-container table {
  border-color: $grey-border;
  border-radius: 5px;
}

.sortLabel:hover {
  cursor: pointer;
}

/*
  Checkbox styling

 */

.filter-checkboxes {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.filter-checkboxes input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: white;
}

.filter-checkboxes:hover input ~ .checkmark {
  background-color: #ccc;
}

.filter-checkboxes:checked ~ .checkmark {
  background-color: #363131;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.filter-checkboxes input:checked ~ .checkmark:after {
  display: block;
}

.filter-checkboxes .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid #363131;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

