@import "Main.scss";
 .shopping-bag-head-container {
    position: fixed;
    padding-top: 0;
  }

  .bag-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 150px;
    padding-bottom: 100px;
  }

.shopping-bag-head-container {
  position: fixed;
  padding-top: 0;
}

.bag-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 100px;
}



/* Box shadow from w3 schools*/
.bag-box {
  width: 700px;
  height: 450px;
  border-radius: 5px;
  box-shadow: 20px 20px 50px grey;


  border-color: #A26D5D;
  border-style: outset;
  overflow: scroll;
}

.bag-box h1 {
  margin: 0;
  font-family: Times New Roman, Serif, serif;
  font-weight: bold;
  font-size: 30px;
  padding-left: 20px;
}

.bag-box h2 {
  font-size: 16px;
  font-family: Times New Roman, Serif, serif;
  //padding-left: 20px;
}

.edit-bag {
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.edit-bag > h2{
  margin-bottom: 150px;
}

//.edit-bag > h2 {
//  padding-left: 300px;
//  padding-bottom: 120px;
//}

.bag > h1 {
  margin: 0;
  font-size: $title_font_size;
  font-family: $title_font;
}

.bag > h2 {
  font-size: $title_font_size;
  font-family: $title_font;
  padding-left: 20px;

}

.tshirt-container {
  width: 150px;
  height: 200px;
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-top: 30px;
}

.tshirt-container > img {
  width: 150px;
  height: 200px;
}

.total-container {
  float: right;
  align-items: center;
  //justify-content: flex-end;
  display: flex;
  flex-direction: column;
  //padding-left: 500px;
}

.checkout-buttons {
  padding-right: 20px;
  padding-top: 90px
}

.checkout-buttons > button {
  background-color: #ccb1a7;
  border: 2px solid $white;
  font-family: $title-font;
  font-size: $title_font_size;
  padding: 5px 10px;
  border-radius: 5px;
  float: right;
}

.checkout-buttons > button:hover {
  background-color: #A26D5D;
  border-color: $grey-border;
  cursor: pointer;
}

.titles {
  margin-right: 20px;
}

.total-container {
  padding-bottom: 55px;
}

.total-container > h3 {
  padding-right: 20px;
}


.tshirt-container, .edit-bag, .total-container {
  display: flex;
  align-items: center;
}

.sub-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

/*
    Quantity styling
    Used reference for the quantity buttons, but used my own images
    https://designmodo.com/shopping-cart-ui/
 */

.edit-quantity {
  display: flex;
  align-items: center;
  margin-top: 30px;
  //margin-right: 60px;
}

.edit-quantity input {
  text-align: center;
  width: 32px;
  font-size: 16px;
  color: black;
  font-weight: 300;
  font-family: $title_font;
  border-color: black;
}

.edit-quantity > button {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.add-btn > img,
.minus-btn > img {
  width: 20px;
  height: 10px;
  margin: 0;
}

.add-btn {
  margin-left: 7.5px;
}



.minus-btn {

  margin-right: 7.5px;
}

.add-btn,
.minus-btn {
  @include basic-button($white);
}

.add-btn:hover,
.minus-btn:hover {
  @include basic-button-hover;
}

